*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}
.all{
  background-color: #ece3ec;
}
.body {
  max-width: 1300px;
  margin: 0 auto;

}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 50px;
}

.product {
  border: 4px solid #f4f4f4;
  padding: 10px;
  border-radius: 20px;
  text-align: center;
  cursor: pointer;
  transition: 0.4s;
}
.product:hover{
  background-color: #8d7487;
}

.product img {
  max-width: 100%;
   /* height: auto;  */
}

.active {
  color: orange;
}

footer {
  text-align: center;
  padding: 10px 0;
  
}

header h1{
  color: rgb(231, 220, 231);
  
  
}
header{
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
 
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  margin-bottom: 40px;
  background-color: #a48a9e;
  box-shadow:  0px 4px 7px  rgb(60, 58, 58);
}